.checkbox{position:relative;cursor:pointer;user-select:none;margin-bottom:22px;transition:all .2s;width:fit-content}.checkbox__input{transition:all .2s;cursor:pointer;position:absolute;opacity:0;height:0;width:0}.checkbox__span{transition:all .2s;position:absolute;height:16px;width:16px;background-color:#eee;margin-left:12px;border-radius:2px;top:50%;margin-top:-7px}.checkbox .checkbox__input+.checkbox__span::after{content:"✓";position:absolute;top:-5px;opacity:0;transition:all .2s}.checkbox .checkbox__input:checked+.checkbox__span{background-color:var(--primary-light)}.checkbox .checkbox__input:checked+.checkbox__span::after{content:"✓";position:absolute;top:-5px;opacity:1;transition:all .2s}.checkbox:hover .checkbox__input+.checkbox__span{transition:all .2s;background-color:var(--primary-light)}.checkbox:focus-within .checkbox__input:not(:checked)+.checkbox__span{transition:all .2s;background-color:var(--secondary)}.checkbox:focus-within .checkbox__input:checked+.checkbox__span{transition:all .2s;outline:2px solid var(--secondary)}.checkbox:hover .checkbox__input:checked+.checkbox__span{transition:all .2s;background-color:var(--secondary)}